'use strict';
angular.module('videoHub')
  //TODO: These constants are redundant between the detail pages. Should be merged together into one mega-constant.
  .constant('VideoDetailControllerConstants', {
    SAVED_ALERT_MESSAGE: 'Saved',
    SOMETHING_WENT_WRONG_ALERT_MESSAGE: 'Something went wrong with saving the Video. Please call your friendly sysadmin.',
    PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE: 'Please fill in all required fields.',
    DELETE_SUCCESSFUL: 'Delete successful'
  })
  .controller('VideoDetailController', function($scope, $routeParams, VideoService, $location, AlertService, VideoDetailControllerConstants) {
    $scope.video = {};

    $scope.getVideo = function() {
      if (_.isUndefined($routeParams.videoId)) {
        $scope.video = {};
      } else {
        return VideoService.one($routeParams.videoId).get().then(
          $scope.setVideo,
          function(data) {
            console.log(data);
          }
        );
      }
    };

    $scope.setVideo = function(data) {
      if (_.isEmpty(data.input)) {
        data.input = '';
      }
      $scope.video = data;
      if (_.isUndefined($scope.video.tags)) {
        $scope.video.tags = [];
      }
      $scope.tagObjects = $scope.video.tags;
      $scope.video.tags = _.pluck($scope.video.tags, 'id');
      $scope.channel = $scope.video.channel;
      $scope.series = $scope.video.series;
    };

    $scope.saveVideo = function() {
      if ($scope.videoDetailForm.$valid) {
        if (_.has($scope.channel, 'id')) {
          $scope.video.channel = $scope.channel.id;
        }

        if (_.has($scope.series, 'id')) {
          $scope.video.series = $scope.series.id;
        }

        $scope.video.tags = _.pluck($scope.tagObjects, 'id');
        if ($routeParams.videoId) {
          $scope.video.save().then(displaySaveSuccessfulAlert, displaySomethingWentWrongAlert);
        } else {
          VideoService.post($scope.video).then(navigateToNewlyCreatedEditPage, displaySomethingWentWrongAlert);
        }
      } else {
        AlertService.error(VideoDetailControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
      }
    };

    function navigateToNewlyCreatedEditPage(response) {
      displaySaveSuccessfulAlert().then(function() {
        $location.path('cms/edit/' + response.id + '/');
      });
    }

    function displaySaveSuccessfulAlert() {
      return AlertService.success(VideoDetailControllerConstants.SAVED_ALERT_MESSAGE, true);
    }

    function displaySomethingWentWrongAlert() {
      AlertService.error(VideoDetailControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    }

    $scope.delete = function() {
      $scope.video.remove().then(function() {
        AlertService.success(VideoDetailControllerConstants.DELETE_SUCCESSFUL, true).then(function() {
          $location.path('cms/');
        });
      });
    };
    $scope.getVideo();
  });
